body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.clock {
  position: absolute;
  color: white;
  padding: 0;
  margin: 0;
  -webkit-animation: rotateClock 1.5s linear infinite;
          animation: rotateClock 1.5s linear infinite;
}

@-webkit-keyframes rotateClock {
  0% {
    transform: rotate(0deg) scale(1.2);
  }
  10% {
    transform: rotate(36deg) scale(1.1);
  }
  20% {
    transform: rotate(72deg) scale(1.2);
  }
  30% {
    transform: rotate(108deg) scale(1.1);
  }
  40% {
    transform: rotate(144deg) scale(1.2);
  }
  50% {
    transform: rotate(180deg) scale(1.1);
  }
  60% {
    transform: rotate(216deg) scale(1.2);
  }
  70% {
    transform: rotate(252deg) scale(1.1);
  }
  80% {
    transform: rotate(288deg) scale(1.2);
  }
  90% {
    transform: rotate(324deg) scale(1.1);
  }
  100% {
    transform: rotate(360deg) scale(1.2);
  }
}

@keyframes rotateClock {
  0% {
    transform: rotate(0deg) scale(1.2);
  }
  10% {
    transform: rotate(36deg) scale(1.1);
  }
  20% {
    transform: rotate(72deg) scale(1.2);
  }
  30% {
    transform: rotate(108deg) scale(1.1);
  }
  40% {
    transform: rotate(144deg) scale(1.2);
  }
  50% {
    transform: rotate(180deg) scale(1.1);
  }
  60% {
    transform: rotate(216deg) scale(1.2);
  }
  70% {
    transform: rotate(252deg) scale(1.1);
  }
  80% {
    transform: rotate(288deg) scale(1.2);
  }
  90% {
    transform: rotate(324deg) scale(1.1);
  }
  100% {
    transform: rotate(360deg) scale(1.2);
  }
}
