* {
    font-weight: 300;
}

body {
    margin: 0;
    padding: 0;
}

.container {
    padding: 0;
    width: 100%;
    margin: 30px 0 0 0;
    justify-content: center;
    align-content: center;
    text-align: center;
}

.header {
    position: relative;
    background: #5C97FF;
    overflow: hidden;
    height:100%;
    width: 100%;
    display: block;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    background-image: url('https://images.unsplash.com/photo-1525921429624-479b6a26d84d?ixlib=rb-1.2.1&auto=format&fit=crop&w=1050&q=80');
    background-repeat: no-repeat;
    -ms-background-size: cover;
    background-size: cover;
    opacity: 1;
}

.overlay {
    width: 100%;
    height: 100%;
    z-index: 1;
    background-color: #000;
    opacity: 0.8;
}

.logo {
    width: 50%;
    padding: 0;
    margin: 0;
    justify-self: center;
}

.left {
    display: flex;
    width: 40%;
    flex-direction: column;
    margin: 80px 0;
}

p {
    display: inline-block;
    font-family: 'Josefin Sans', sans-serif;
    color: white;
}

.stalk {
    text-align: center;
    width: 50%;
    font-family: 'Josefin Sans', sans-serif;
    letter-spacing: 5px;
    margin: 40px auto;
    font-size: 120%;
}

.subheading {
    text-align: left;
    font-family: 'Josefin Sans', sans-serif;
    font-size: 4vw;
    line-height: 120%;
}

.clock {
    top: 9.2vw;
    left: 74.5vw;
}

.mainheading {
    padding: 20px 0 10px 0;
    font-family: 'Josefin Sans', sans-serif;
    font-weight: 700;
    font-size: 8vw;
    line-height: 80%;
    -webkit-animation: pulsebeat 1.5s linear infinite;
            animation: pulsebeat 1.5s linear infinite;
}

.content {
    margin: 0 70px 0 60px;
    width: 40%;
    height: 530px;
    color: white;
}

.social {
    display: flex;
    width: 70%;
    margin: 0 auto;
    justify-content: space-evenly;
}

.icon {
    opacity: 0.3;
    padding:5px;
}

.icon:hover {
    color: #E17A2A;
    box-shadow: 0 0 15px #E17A2A;
    opacity: 1;
    transform: scale(1.1);
    border-radius:5px;
}

a {
    text-decoration: none;
    color: white;
}

a:hover,a:active {
    text-decoration: none;
    color: white;
}

input, button {
    width: 300px;
    border: none;
    background: white;
    padding: 10px 35px;
    border-radius: 0 !important;
}
button {
    width: 280px;
    background: #E17A2A;
    color: white;
    font-size: 110%;
    font-family: 'Josefin Sans', sans-serif;
}

.paragraph {
    text-align: left;
    padding: 0 20px 5px 20px;
    width: 70%;
}

.paragraph p {
    font-size: 1.6rem;
    font-family: 'Josefin Sans', sans-serif;
    line-height: 110%;
}

@-webkit-keyframes pulsebeat {
    0% {
        transform: scale(1.1);
        opacity: 1;
    }
    50% {
        transform: scale(1);
        opacity: 0.4;
    }
    100% {
        transform: scale(1.1);
        opacity: 1;
    }
}

@keyframes pulsebeat {
    0% {
        transform: scale(1.1);
        opacity: 1;
    }
    50% {
        transform: scale(1);
        opacity: 0.4;
    }
    100% {
        transform: scale(1.1);
        opacity: 1;
    }
}
@media only screen and (min-width: 1600px) {
    .mainheading {
        font-size: 7vw;
    }
}


@media only screen and (max-width: 1520px)and (min-width: 1419px) {
    .clock {
        top: 9vw;
        left: 74vw;
    }
    .mainheading {
        font-size: 7vw;
    }
}

@media only screen and (max-width: 1418px)and (min-width: 1200px) {
    .clock {
        top: 8vw;
        left: 75vw;
    }
}

@media only screen and (max-width: 1199px)and (min-width: 868px) {
    .clock {
        top: 7vw;
        left: 76vw;
    }
    .mainheading {
        font-size: 6vw;
    }
}

@media only screen and (max-width: 867px)and (min-width: 768px) {
    .clock {
        top: 7vw;
        left: 80vw;
    }
    .mainheading {
        font-size: 6vw;
    }
}

@media only screen and (max-width: 499px) and (min-width: 100px) {
/* import this stylesheet in grid.css*/
/*For Mobile*/
    * {
        font-weight: 300;
    }

    body {
        padding: 0;
        margin: 0;
    }

    .container {
        display: flex;
        flex-direction: column;
        margin: 1px auto;
        width: 90%;
        padding: 0;
    }

    .clock {
        position: fixed;
        top: 20px;
        left: 42%;
        opacity: 0.2;
    }

    body {
        margin: 0;
        padding: 0;
        overflow-x: hidden;
    }

    .logo{
        display: block;
        width: 50%;
        margin: 1px auto;
    }
    
    .stalk {
        text-align: center;
        width: 80%;
        margin: 20px auto 0 auto;
        font-size: 0.6rem;
        letter-spacing: 3px;
    }
    .social{
        width: 10%;
        margin: 0 auto;
    }

    .subheading {
        text-align: center;
        width: 90%;
        margin: 0 auto;
        padding:4px 0 4px 0;
        line-height: 130%;
        font-size: 1rem;
    }
    
    .mainheading {
        width:  100%;
        text-align: center;
        margin: 0;
        line-height: 150%;
        font-size: 2.5rem;
    }
    
    .email {
        height: 20px;
    }
    
    button {
        width: 40%;
        padding: 0;
        font-size: 0.3rem;
    }

    input {
        padding: 0;
        width: 50%;
    }
    
    .paragraph {     
        margin: 0 auto;
        width: 90%;
        text-align: center;
    }

    .paragraph p {
        font-size: 0.6rem;
        line-height: 90%;
        text-align: center;
    }

    .email {
        text-align: center;
        width: 80%;
        margin: 0 auto;
    }

    input {
        width: 60%;
    }
    button {
        width: 40%;
    }

    .content {
        width: 90%;
        display: flex;
        flex-direction: column;
        margin: 60px auto;
        justify-items: center;
        padding: 6px;
        height: auto;
    }

    .left {
        border-top: 1px solid white;
        width: 100%;
        margin: 0 auto;
        order:2;
        padding: 0;
    }
    
}
/*For Tablet*/
@media only screen and (max-width: 768px)and (min-width: 500px) {
    .container {
        display: flex;
        flex-direction: column;
        width: 90%;
        margin: 10px auto;
    }

    .left {
        border-top: 1px solid white;
        width: 100%;
        padding: 0;
        margin: 16vw auto 0 auto;
        order: 2;
    }

    .logo {
        width: 100%;
        padding: 0;
        margin: 0;
    }

    .content {
        order: 1;
        width: 90%;
        margin: 0 auto;
        height: auto;
    }
    
    .clock {
        top: 25px;
        left: 48%;
    }

    .subheading {
        margin: 0;
        padding: 0;
        margin-top: 60px;
        text-align: center;
    }

    .mainheading {
        font-size: 280%;
    }

    .paragraph {
        padding: 10px 0;
        margin: 1px auto;
        width: 100%;
        text-align: center;
    }

    .paragraph p {
        width: 70%;
        padding:0 0 20px 0;
        margin: 0 auto;
        font-size: 150%;
    }

    input, .email {
        margin: 0;
    }
}
/* Divider */
@media only screen and (max-width: 768px) and (min-width: 200px) {
    #divider {
        display: none;
    }
}
